import React, { useState } from 'react';
// import UnprintedInvoice from './components/UnprintedInvoice'
// import MultiplePrintedInvoice from './components/MultiplePrintedInvoice'
import LostItem from './components/LostItem'
import Invoice from './components/Invoice'
import 'bulma';

const App = () => {
  const [menu, setMenu] = useState("lostItem")
  return (
    <div className="App">
      <div className="container">
        <div className="tabs">
          <ul>
            <li><button className={`button ${menu === "lostItem" ? " is-primary is-light" : " is-white"}`} onClick={() => setMenu("lostItem")}><strong>BARANG HILANG</strong></button></li>
            <li><button className={`button ${menu === "invoice" ? " is-primary is-light" : " is-white"}`} onClick={() => setMenu("invoice")}><strong>INVOICE</strong></button></li>
          </ul>
        </div>
        <div style={ menu === "lostItem" ? {} : {display: "none"}}>
          <LostItem/>
        </div>
        <div style={ menu === "invoice" ? {} : {display: "none"}}>
          <Invoice/>
        </div>
      </div>
    </div>
  );
}

export default App;
