import React, { useState, useRef } from "react"
import axios from "axios"

const Invoice = (props) => {
  const [salesFileName, setSalesFileName] = useState("File Belum Dipilih")
  const [stockOutFileName, setStockOutFileName] = useState("File Belum Dipilih")
  const [isLoading, setLoading] = useState(false)
  const [status, setStatus] = useState(null)
  
  const salesFile = useRef(null)
  const stockOutFile = useRef(null)

  const onChooseSalesFile = (event) => {
    if (event && event.target && event.target.files[0]) {
      setSalesFileName(event.target.files[0].name)
    } else {
      setSalesFileName("File Belum Dipilih")
    }
  }

  const onChooseStockOutFile = (event) => {
    if (event && event.target && event.target.files[0]) {
      setStockOutFileName(event.target.files[0].name)
    } else {
      setStockOutFileName("File Belum Dipilih")
    }
  }

    const onSubmit = (event) => {
      event.preventDefault()
      setLoading(true)
      setStatus(null)

      const formData = new FormData()
      formData.append("salesFile", salesFile.current.files[0])
      formData.append("stockOutFile", stockOutFile.current.files[0])
      
      const submitFile = async() => {
        try {
          const response = await axios({
            method: "POST",
            url: "http://localhost:43000/report/download-invoice-report",
            data: formData
          })
          setStatus(response.data)
          setLoading(false)
        } catch (err) {
          setStatus("Something when wrong")
          setLoading(false)
        }
      }
      submitFile()
    }

  const renderDownload = () => {
    if (isLoading) {
      return (
        <div>
          <hr/><progress className="progress is-small is-primary" max="100">15%</progress>
        </div>
      )
    }
    if (status === "COMPLETED") {
      return (
        <div>
          <hr/>
          <h3>Your report is ready</h3>
          <a href="http://localhost:43000/report/download-unprinted-invoice-report">Download Laporan Belum Cetak</a>
          <br/>
          <a href="http://localhost:43000/report/download-multiple-printed-invoice-report">Download Laporan Double Cetak</a>
        </div>
      )
    }
    
    if (status) {
      return (
        <div>
          <hr/>
          <h3>{status}</h3>
        </div>
      )
    }
  }

    return (
        <div className="box">
          <div className="notification">
            <strong>LAPORAN INVOICE</strong>
          </div>
          <form onSubmit={onSubmit}>
            <fieldset disabled={isLoading}>
                <div className="field">
                <label className="label">Laporan Penjualan</label>
                <div className="control">
                    <div className="file has-name">
                    <label className="file-label">
                        <input className="file-input" type="file" name="salesFile" ref={salesFile} onChange={(event) => onChooseSalesFile(event)}/>
                        <span className="file-cta">
                        <span className="file-icon">
                            <i className="fas fa-upload"></i>
                        </span>
                        <span className="file-label">
                            Pilih File
                        </span>
                        </span>
                        <span className="file-name">
                          { salesFileName || "File Belum Dipilih" }
                        </span>
                    </label>
                    </div>
                </div>
                </div>
                <div className="field">
                <label className="label">Laporan Barang Keluar</label>
                <div className="control">
                    <div className="file has-name">
                    <label className="file-label">
                        <input className="file-input" type="file" name="stockOutFile" ref={stockOutFile} onChange={(event) => onChooseStockOutFile(event)}/>
                        <span className="file-cta">
                        <span className="file-icon">
                            <i className="fas fa-upload"></i>
                        </span>
                        <span className="file-label">
                            Pilih File
                        </span>
                        </span>
                        <span className="file-name">
                          { stockOutFileName || "File Belum Dipilih" }
                        </span>
                    </label>
                    </div>
                </div>
                </div>
                <div className="field is-grouped">
                    <p className="control">
                        <button type="submit" className="button is-primary">Generate Laporan</button>
                    </p>
                </div>
            </fieldset>
          </form>
          { renderDownload() }
        </div>
    )
}

export default Invoice