import React, { useState, useRef } from "react"
import axios from "axios"

const LostItem = (props) => {
  const [salesFileName, setSalesFileName] = useState("File Belum Dipilih")
  const [lostItemsFileName, setLostItemsFileName] = useState("File Belum Dipilih")
  const [paymentFileName, setPaymentFileName] = useState("File Belum Dipilih")
  const [returnedItemsFileName, setReturnedItemsFileName] = useState("File Belum Dipilih")
  //const [lostItemsFile, setLostItemsFile] = useState("")
  const [isLoading, setLoading] = useState(false)
  const [status, setStatus] = useState(null)
  
  const lostItemsFile = useRef("")
  const salesFile = useRef("")
  const paymentFile = useRef("")
  const returnedItemsFile = useRef("")

  const onChooseSalesFile = (event) => {
    if (event && event.target && event.target.files[0]) {
      setSalesFileName(event.target.files[0].name)
    } else {
      setSalesFileName("File Belum Dipilih")
    }
  }

  const onChooseLostItemsFile = (event) => {
    if (event && event.target && event.target.files[0]) {
      setLostItemsFileName(event.target.files[0].name)
    } else {
      setLostItemsFileName("File Belum Dipilih")
    }
  }

  const removeLostItemsFile = () => {
    lostItemsFile.current.value = ""
    setLostItemsFileName("File Belum Dipilih")
  }

  const onChoosePaymentFile = (event) => {
    if (event && event.target && event.target.files[0]) {
      setPaymentFileName(event.target.files[0].name)
    } else {
      setPaymentFileName("File Belum Dipilih")
    }
  }

  const onChooseReturnedItemsFile = (event) => {
    if (event && event.target && event.target.files[0]) {
      setReturnedItemsFileName(event.target.files[0].name)
    } else {
      setReturnedItemsFileName("File Belum Dipilih")
    }
  }

    const onSubmit = (event) => {
      event.preventDefault()
      setLoading(true)
      setStatus(null)

      const formData = new FormData()
      if (lostItemsFile && lostItemsFile.current && lostItemsFile.current.files[0]) {
        formData.append("lostItemsFile", lostItemsFile.current.files[0])
      }

      if (salesFile && salesFile.current && salesFile.current.files[0]) {
        formData.append("salesFile", salesFile.current.files[0])
      } else {
        setStatus("Laporan Penjualan belum dipilih")
        setLoading(false)
        return
      }

      if (paymentFile && paymentFile.current && paymentFile.current.files[0]) {
        formData.append("paymentFile", paymentFile.current.files[0])
      } else {
        setStatus("Laporan Uang Masuk belum dipilih")
        setLoading(false)
        return
      }
      
      if (returnedItemsFile && returnedItemsFile.current && returnedItemsFile.current.files[0]) {
        formData.append("returnedItemsFile", returnedItemsFile.current.files[0])
      } else {
        setStatus("Laporan Barang Retur belum dipilih")
        setLoading(false)
        return
      }
      
      const submitFile = async() => {
        try {
          const response = await axios({
            method: "POST",
            url: "http://localhost:43000/report/download-lost-items-report",
            data: formData
          })
          setStatus(response.data)
          setLoading(false)
        } catch (err) {
          setStatus("Something when wrong")
          setLoading(false)
        }
      }

      submitFile()
    }

  const renderDownload = () => {
    if (isLoading) {
      return (
        <div>
          <hr/><progress className="progress is-small is-primary" max="100">15%</progress>
        </div>
      )
    }
    if (status === "COMPLETED") {
      return (
        <div>
          <hr/>
          <h3>Your report is ready</h3>
          <a href="http://localhost:43000/report/download-lost-items-report">Download Laporan Barang Hilang</a>
        </div>
      )
    }
    
    if (status) {
      return (
        <div>
          <hr/>
          <h3>{status}</h3>
        </div>
      )
    }
  }

    return (
        <div className="box">
          <div className="notification">
            <strong>LAPORAN BARANG HILANG</strong>
          </div>
          <form onSubmit={onSubmit}>
            <fieldset disabled={isLoading}>
                <div className="field">
                <label className="label">Laporan Barang Hilang (Previous)</label>
                <div className="control">
                    <div className="file has-name">
                    <label className="file-label">
                        <input className="file-input" type="file" name="lostItemsFile" ref={lostItemsFile} onChange={(event) => onChooseLostItemsFile(event)} />
                        <span className="file-cta">
                        <span className="file-icon">
                            <i className="fas fa-upload"></i>
                        </span>
                        <span className="file-label">
                            Pilih File
                        </span>
                        </span>
                        <span className="file-name">
                          { lostItemsFileName || "File Belum Dipilih" }
                        </span>
                        <button type="button" className="button is-warning" onClick={() => removeLostItemsFile()}>Hapus File</button>
                    </label>
                    </div>
                </div>
                </div>
                <div className="field">
                <label className="label">Laporan Penjualan</label>
                <div className="control">
                    <div className="file has-name">
                    <label className="file-label">
                        <input className="file-input" type="file" name="salesFile" ref={salesFile} onChange={(event) => onChooseSalesFile(event)}/>
                        <span className="file-cta">
                        <span className="file-icon">
                            <i className="fas fa-upload"></i>
                        </span>
                        <span className="file-label">
                            Pilih File
                        </span>
                        </span>
                        <span className="file-name">
                          { salesFileName || "File Belum Dipilih" }
                        </span>
                    </label>
                    </div>
                </div>
                </div>
                <div className="field">
                  <label className="label">Laporan Uang Masuk</label>
                  <div className="control">
                      <div className="file has-name">
                      <label className="file-label">
                          <input className="file-input" type="file" name="paymentFile" ref={paymentFile} onChange={(event) => onChoosePaymentFile(event)}/>
                          <span className="file-cta">
                          <span className="file-icon">
                              <i className="fas fa-upload"></i>
                          </span>
                          <span className="file-label">
                              Pilih File
                          </span>
                          </span>
                          <span className="file-name">
                            { paymentFileName || "File Belum Dipilih" }
                          </span>
                      </label>
                      </div>
                  </div>
                </div>
                <div className="field">
                  <label className="label">Laporan Barang Retur</label>
                  <div className="control">
                      <div className="file has-name">
                      <label className="file-label">
                          <input className="file-input" type="file" name="returnedItemsFile" ref={returnedItemsFile} onChange={(event) => onChooseReturnedItemsFile(event)}/>
                          <span className="file-cta">
                          <span className="file-icon">
                              <i className="fas fa-upload"></i>
                          </span>
                          <span className="file-label">
                              Pilih File
                          </span>
                          </span>
                          <span className="file-name">
                            { returnedItemsFileName || "File Belum Dipilih" }
                          </span>
                      </label>
                      </div>
                  </div>
                </div>
                <div className="field is-grouped">
                    <p className="control">
                        <button type="submit" className="button is-primary">Generate Laporan</button>
                    </p>
                </div>
            </fieldset>
          </form>
          { renderDownload() }
        </div>
    )
}

export default LostItem